.login{
    position: absolute;
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: rgba(13, 45, 45, 0.722);

}
.login form{
    display: grid;
    width:80%;
    height:200px;
    margin:10px auto;
}
.form{
    width:30%;
        margin:10px auto;
    background-color: white;
    height: fit-content;
    box-shadow: 3px 5px 20px -2px orange;
    padding:1px 10px 10px 10px;
    }
    .form h3{
    text-align: center;
    font-size: 40px;
    font-family: 'Courier New', Courier, monospace;
    }
   .login form input[type="password"],input[type="email"]{
        background: none;
        border:none;
        border-bottom:2px solid purple;;
        outline:0;
    }
    .login form input:focus{
        box-shadow: 0px 0px 5px 1px purple;
    }
    .login form input[type="submit"]{
    
        color:white;
        background-color:purple;
        outline:0;
        opacity: 0.4;
        cursor: not-allowed;
    }
    .Oauth{
        margin:10px auto;
        display: flex;
        height: 30px;
        width: 80%;
        transition-delay: .5s;
    }
    .Oauth div{
        width: 100%;
        box-shadow: 0px 0px 5px 1px rgb(0, 0, 0);
        margin:0px 2px;
    }
    .Oauth img{
        width:100%;
        height: 100%;
    }
    @media only screen and (max-width:600px){
        .form{
         width:80%;   
        }
    }